.error-message {
  width: 100%;
  margin-top: 0.25rem;
  font-size: 0.875em;
  color: #dc3545;
}
.btn_upload {
  color: #fff;
  position: relative;
  display: flex;
  overflow: hidden;
  z-index: 3;
  white-space: nowrap;
}

.btn_upload input {
  position: absolute;
  width: 100%;
  left: 0;
  top: 0;
  width: 100%;
  height: 105%;
  cursor: pointer;
  opacity: 0;
}
.card-img {
  min-height: 370px;
  object-fit: cover;
}
.profile-pic2 {
  width: 57px;
  height: 57px;
  overflow: hidden;
  border-radius: 50%;
  position: relative;
  object-fit: cover;
}

.btn-spinner > div {
  width: 12px;
  height: 12px;
  background-color: #fff;
  border-radius: 100%;
  display: inline-block;
  -webkit-animation: sk-bouncedelay 1.4s infinite ease-in-out both;
  animation: sk-bouncedelay 1.4s infinite ease-in-out both;
}

.about-calendar-border {
  border-radius: 12px 0px 0px 12px;
}

.css-1ofr53f-control,
.css-qu9om8-control {
  border: 2px solid #364072 !important;
  padding: 0.5em 1.5rem !important;
  border-radius: 12px !important;
}
.side-navigation .navigation {
  font-size: 20px;
}
.side-navigation {
  min-width: 300px;
}
.card-image-performance {
  max-height: 300px !important;
  min-height: 0px !important;
  border-radius: 15px !important;
}
.content-icon img {
  height: 23px;
}

.pagination {
  margin: 30px auto;
  display: flex;
  list-style: none;
  outline: none;
  float: right;
  border-radius: 10px;
  font-weight: 400;
  bottom: 10px;
}
.pagination > .active > a {
  background-color: #364072;
  border-color: #fff;
  color: #fff;
}
.pagination > li > a {
  border: 1px solid #6dcaf0;
  padding: 5px 10px;
  outline: none;
  cursor: pointer;
}
.pagination > .active > a,
.pagination > .active > span,
.pagination > .active > a:hover,
.pagination > .active > span:hover,
.pagination > .active > a:focus,
.pagination > .active > span:focus {
  background-color: #6dcaf0;
  border-color: #6dcaf0;
  outline: none;
}
.pagination > li > a,
.pagination > li > span {
  color: #fff;
}
.pagination > li:first-child > a,
.pagination > li:first-child > span {
  border-radius: 5px 0px 0px 5px;
}

.pagination > li:last-child > a,
.pagination > li:last-child > span {
  border-radius: 0px 5px 5px 0px;
}
.card-image-performance-details {
  min-height: 0px !important;
  border-radius: 15px !important;
}
.advanced-search {
  height: 100% !important;
  overflow-x: auto !important;
}
.collab-profile-img {
  max-width: 43px;
  max-height: 43px;
  min-height: 43px;
  min-width: 43px;
  overflow: hidden;
  border-radius: 50%;
  object-fit: cover;
}

.pagination-height {
  height: 100vh;
}

img.img-card-top {
  width: 100%;
  height: 35vh;
  object-fit: cover;
  border-radius: 15px;
}

img.img-card-collab{
  width: 100%;
  height: 22vh;
  object-fit: cover;
}
